@import '../vars/index';

body {
    background-color: $body-background-color;
    font-family: 'Roboto', sans-serif;

    @include respond-to(mobile) {
        nav {
            &>img {
                width: 30%;
                height: 30%;
                padding: 10px;
            }
        }

        .header-container {
            align-items: center;
            justify-content: center;
            flex-direction: column;
            margin-top: 30px;

            &__text {
                text-align: center;

                &--p {
                    font-size: $global-XS-font-size;

                    &--h1 {
                        font-size: $global-M-font-size;

                        &--p {
                            font-size: $global-XS-font-size;

                            &--p {
                                font-size: 7px;
                            }
                        }
                    }
                }
            }

            &__img {
                width: 70%;
                padding-right: 15%;
            }
        }

        //form

        .form-container {
            margin-top: 35%;

            &__text {

                &--inputs {
                    flex-direction: column;

                    &--input,
                    &--select,
                    &--button {
                        height: 33px;
                        width: 183px;

                    }
                }
            }
        }

        //offer
        .offer-bold-text {
            margin-right: 50px;
            font-size: 30px;
        }

        .offer-text {
            margin-right: 50px;
            font-size: 20px;
        }


        .offer-grid-container {
            &__ol {
                grid-column-end: 8;
                grid-row-end: 4;

                &--li {

                    &--img {
                        grid-column-start: 1;
                        grid-column-end: 2;
                        padding-right: 25px;

                        &--h4 {
                            grid-column-start: 4;
                            grid-column-end: 6;

                            &--p {
                                grid-column-start: 4;
                                grid-column-end: 6;
                            }
                        }
                    }
                }
            }

            &__img {
                grid-column-start: 3;
                grid-column-end: 7;
                grid-row-start: 6;
                grid-row-end: 10;
                width: 270px;
                height: 350px;
            }
        }

        //info

        .info-container {
            justify-content: center;
            flex-direction: column;
            letter-spacing: 0;
            background-color: $white-color;
            width: 60%;
            margin-right: 20%;

            &__icon {
                &--link {
                    color: $green-bold-color;
                    font-weight: bolder;
                }
            }

        }

        //howWorks

        .worksHead-container {
            &__text {
                grid-column-start: 1;
                grid-column-end: 6;
                grid-row-start: 1;
                grid-row-end: 2;

                &--bold {
                    grid-column-start: 1;
                    grid-column-end: 6;
                    grid-row-start: 1;
                    grid-row-end: 2;
                    font-size: 35px;
                    margin-right: 50px;
                }

                &--p {
                    grid-column-start: 1;
                    grid-column-end: 6;
                    grid-row-start: 1;
                    grid-row-end: 2;
                    font-size: 20px;
                    margin-right: 50px;
                }
            }

            &__img {
                grid-column-start: 3;
                grid-column-end: 7;
                grid-row-start: 3;
                grid-row-end: 5;
                height: 250px;
                width: 200px;
            }

        }

        //whyUs
        .whyUs-container {
            grid-template-columns: 16% 16% 16% 16% 16% 16%;

            &__text {
                grid-column-start: 2;
                grid-column-end: 6;
                grid-row-start: 1;
                grid-row-end: 3;

                &--bold {
                    font-size: 35px;
                    margin-right: 0px;

                    &--p {
                        font-size: 20px;
                        margin-right: 0px;
                    }
                }
            }

            &__img {
                grid-column-start: 2;
                grid-column-end: 5;
                grid-row-start: 5;
                grid-row-end: 6;
                height: 90px;
                width: 350px;
            }
        }

        //callUs
        .callUs-container {
            margin-top: 300px;
            flex-direction: column;

            &__text {
                &--img {
                    width: 300px;
                    height: 180px;
                }
            }
        }

        //footer
        .footer-container__link--img {
            width: 25px;
            height: 25px;
            padding-left: 0;
        }

        .logo-img {
            width: 130px;
            height: 35px;
            padding: 30px;
        }
    }

    @include respond-to(tablet) {

        //nav
        nav {
            &>img {
                width: 20%;
                height: 20%;
                padding: 20px;

            }
        }

        //header
        .header-container {
            padding-right: 10px;

            &__text {
                color: $text-color;
                width: 50%;

                &--p {
                    font-size: $global-XS-font-size;

                    &--h1 {
                        font-size: $global-M-font-size;

                        &--p {
                            font-size: $global-XS-font-size;

                            &--p {
                                font-size: 7px;
                            }
                        }
                    }
                }
            }

            &__img {
                width: 70%;
                padding: 0px;
            }
        }

        //form
        .form-container {
            margin-top: 20%;

            &__text {
                &--inputs {

                    &--input,
                    &--select,
                    &--button {
                        height: 33px;
                    }
                }
            }
        }

        //offer

        .offer-grid-container {
            &__ol {
                grid-column-end: 8;
                grid-row-end: 4;

                &--li {

                    &--img {
                        grid-column-start: 1;
                        grid-column-end: 2;
                        padding-right: 25px;

                        &--h4 {
                            grid-column-start: 2;
                            grid-column-end: 5;


                            &--p {
                                grid-column-start: 2;
                                grid-column-end: 5;
                            }
                        }
                    }
                }
            }

            &__img {
                grid-row-start: 3;
                grid-row-end: 9;
                width: 270px;
                height: 350px;
            }
        }

        //howWorks
        .worksHead-container {
            &__text {
                grid-row-start: 1;
                grid-row-end: 5;

                &--bold {

                    grid-row-start: 1;
                    grid-row-end: 5;
                    font-size: 40px;
                }

                &--p {
                    grid-row-start: 1;
                    grid-row-end: 5;
                    font-size: 20px;
                }
            }

            &__img {
                grid-column-start: 5;
                grid-column-end: 7;
                height: 270px;
                width: 270px;
            }
        }

        //whyUs

        .whyUs-container {
            &__text {
                grid-column-start: 1;
                grid-column-end: 4;
                grid-row-start: 1;
                grid-row-end: 3;

                &--bold {
                    font-size: 30px;

                    &--p {
                        font-size: 15px;
                    }
                }
            }

            &__img {
                grid-column-start: 4;
                grid-column-end: 7;
                grid-row-start: 3;
                grid-row-end: 5;
                height: 70px;
                width: 300px;
            }

        }

        //callUs
        .callUs-container__text--img {
            width: 300px;
            height: 180px;
        }

        //footer
        .footer-container__link--img {
            width: 25px;
            height: 25px;
            padding-left: 0;
        }

        .logo-img {
            width: 130px;
            height: 35px;
            padding: 30px;
        }
    }

    @include respond-to(desktop) {

        //header
        .header-container {

            &__text {
                width: 70%;

                &--p {
                    &--h1 {
                        font-size: $global-L-font-size;
                    }
                }
            }

            &__img {
                height: 80%;
                width: 80%;
                margin-right: 50px
            }
        }

        //form
        .form-container {
            &__text {
                &--p {
                    &--inputs {
                        &--input {
                            width: 130px;
                        }

                        &--select {
                            width: 200px;
                        }

                        &--button {
                            width: 200px;
                        }

                    }
                }
            }
        }
    }

    @include respond-to(LDesktop) {
        max-width: 1440px;
        margin: 0 auto;
    }
}